@use '../util' as *;

.skills {
  position: relative;
  width: 100%;
  max-width: var(--max-width);
  padding-inline: clamp(rem(16), 4vw, rem(32));
  margin-inline: auto;
  padding-block-end: clamp(rem(80), 10vw, rem(140));
  overflow-x: hidden;

  &::before {
    content: '';
    display: block;
    height: rem(1);
    background: var(--clr-white);
  }

  &__wrapper {
    padding-block: clamp(rem(40), 6vw, rem(80));
    display: flex;
    flex-direction: column;
    gap: rem(24);
    overflow-x: hidden;

    &::after {
      content: '';
      position: absolute;
      display: block;
      background-image: var(--header-bg-start);
      bottom: rem(15);
      right: rem(-340);
      height: rem(129);
      width: rem(530);
    }
  }

  @include breakpoint(tablet) {
    padding-inline: clamp(rem(30), 4vw, rem(60));

    hr {
      display: none;
    }

    &__wrapper {
      padding-block-start: clamp(rem(52), 8vw, rem(100));
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(3, 1fr);
      grid-column-gap: rem(32);
      grid-row-gap: rem(52);

      &::after {
        bottom: initial;
        top: rem(387);
        right: rem(-400);
      }
    }
  }

  @include breakpoint(desktop) {
    padding: 0;
    overflow: visible;

    &__wrapper {
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(2, 1fr);
      grid-column-gap: 0;
      grid-row-gap: rem(58);

      &::after {
        bottom: initial;
        top: rem(253);
      }
    }
  }
}

.skill {
  display: flex;
  flex-direction: column;
  text-align: center;
  gap: rem(1);

  &-name {
    font-size: rem(32);
    line-height: rem(40);
    letter-spacing: rem(-1);
    margin: 0;
    padding: 0;
  }

  &-years {
    font-size: rem(16);
    line-height: rem(26);
    font-weight: 400;
  }

  @include breakpoint(tablet) {
    gap: rem(14);
    text-align: left;

    &-name {
      font: var(--heading-l);
      letter-spacing: rem(-1.5);
    }

    &-years {
      font: var(--body);
    }
  }
}
