@use '../util' as *;

:root {
  // Colors
  --clr-primary: #4ee1a0;
  --clr-error: #ff6f5b;
  --clr-black: #151515;
  --clr-gray-dark: #242424;
  --clr-gray-light: #d9d9d9;
  --clr-white: #ffffff;

  // Header Background
  --header-bg-start: url(../../../public/images/pattern-rings.svg);
  --header-bg-end: url(../../../public/images/pattern-circle.svg);

  // Typography
  --ff-primary: 'Space Grotesk', sans-serif;

  --heading-xl: normal normal 700 #{rem(88)} / #{rem(88)} var(--ff-primary);
  --heading-l: normal normal 700 #{rem(48)} / #{rem(56)} var(--ff-primary);
  --heading-m: normal normal 700 #{rem(24)} / #{rem(32)} var(--ff-primary);
  --body: normal normal 400 #{rem(18)} / #{rem(28)} var(--ff-primary);

  // Page Layout
  --max-width: #{rem(1110)};

  // Misc
  --overlay-bg: rgba(21, 21, 21, 0.75);
  --box-shadow: 0 0 #{rem(15)} rgba(78, 225, 160, 0.5);
  --box-shadow-error: 0 0 #{rem(15)} rgba(255, 111, 91, 0.5);

  --placeholder: rgba(255, 255, 255, 0.5);
  --placeholder-hover: rgba(78, 225, 160, 0.65);

  --input-outline: rgba(78, 225, 160, 0.25);
  --input-outline-error: rgba(255, 111, 91, 0.25);

  // Transitions
  --transition: all 0.3s ease-in-out;
}
